import React from 'react'

import { Turquoise } from '../global/colors'

function Job(params) {
  return (
    <div>
      <div style={{ marginBottom: 12 }}>
        {params.description}
      </div>
      <h5>Job Responsibilities:</h5>
      <div style={{ marginTop: 12, marginBottom: 12, marginLeft: 12 }}>
        {params.responsibilities.map(text => (
          <div key={text}>
            {text !== '' &&
              <div>
                <i color={Turquoise} className='fas fa-check-circle' style={{ fontSize: 12 }}></i>
                <span style={{ marginLeft: 8, lineHeight: 1 }}>{text}</span>
              </div>
            }
          </div>
        ))}
      </div>
      <h5 style={{ marginBottom: 8 }}>Required Experience:</h5>
      <div style={{ mariginTop: 12, marginBottom: 12, marginLeft: 12 }}>
        {params.experiences.map(text => (
          <div key={text}>
            {text !== '' &&
              <div>
                <i color={Turquoise} className='fas fa-check-circle' style={{ fontSize: 12 }}></i>
                <span style={{ marginLeft: 8, lineHeight: 1 }}>{text}</span>
              </div>
            }
          </div>
        ))}
      </div>
      {params.businessTravel && params.businessTravel !== '0' &&
        <h3> Business Travel: {params.businessTravel}%</h3>
      }
    </div>
  );
}

export default Job;
